
.scrabble {
  position: relative;
  width: 100%;
  height: 100%;
}
.scrabble .title {
    padding: 20px 0;
    font-size: 40px;
    line-height: 1;
    color: #42b983;
    cursor: default;
}
.scrabble .menu {
    box-sizing: border-box;
    border-left: 2px solid #42b983;
    border-top: 2px solid #42b983;
    padding: 120px 0;
    position: absolute;
    z-index: 100;
    top: 0;
    right: -260px;
    width: 260px;
    height: 100%;
    background-color: #eee;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.scrabble .menu.active {
      -webkit-transform: translate3d(-100%, 0, 0);
              transform: translate3d(-100%, 0, 0);
}
.scrabble .menu .btn-wrap {
      margin-bottom: 50px;
}
.scrabble .menu-icon {
    position: absolute;
    z-index: 101;
    top: 40px;
    right: 30px;
    width: 30px;
    height: 25px;
    box-sizing: border-box;
    border-top: 5px solid #42b983;
    border-bottom: 5px solid #42b983;
    background-color: #42b983;
    padding: 5px 0;
    background-clip: content-box;
    cursor: pointer;
    transition: -webkit-transform .1s;
    transition: transform .1s;
    transition: transform .1s, -webkit-transform .1s;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.scrabble .menu-icon.active {
      -webkit-transform: translate3d(-160px, 0, 0);
              transform: translate3d(-160px, 0, 0);
}
.slide-enter-active,
.slide-leave-active {
  transition: all .5s;
}
.slide-enter {
  opacity: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.slide-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.list-enter,
.list-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.list-leave-active {
  position: absolute;
}
.game-title {
  position: absolute;
  left: 50%;
  bottom: 180px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  box-sizing: border-box;
  width: 60%;
  height: 300px;
  border: 2px dashed #f35626;
  margin: 20px auto;
  font-size: 100px;
  line-height: 300px;
  color: #f35626;
  background-image: -webkit-linear-gradient(92deg, #f35626, #feab3a);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: hue 60s infinite linear;
          animation: hue 60s infinite linear;
}
.card-wrap {
  font-size: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.card-wrap li {
    display: inline-block;
    width: 50px;
    margin-right: 10px;
    padding: 50px 40px;
    border: 2px solid #bbb;
    border-radius: 10px;
    font-size: 60px;
    cursor: pointer;
    transition: -webkit-transform .4s;
    transition: transform .4s;
    transition: transform .4s, -webkit-transform .4s;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
.card-wrap li.select {
      -webkit-transform: translate3d(0, -20%, 0);
              transform: translate3d(0, -20%, 0);
      border-color: #42b983;
      box-shadow: 0 0 5px 1px #42b983;
}
.countdown {
  position: fixed;
  z-index: 200;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 10;
}
.countdown .number {
    position: absolute;
    z-index: 201;
    left: 50%;
    top: 50%;
    width: 120px;
    height: 230px;
    margin-left: -60px;
    margin-top: -165px;
    font-size: 200px;
    opacity: 0;
    -webkit-animation: zoomOut 1s ease 0s 1 both;
            animation: zoomOut 1s ease 0s 1 both;
}
.countdown .number3 {
    z-index: 23;
}
.countdown .number2 {
    z-index: 22;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.countdown .number1 {
    z-index: 21;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}
@-webkit-keyframes zoomOut {
from {
    opacity: 0;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    opacity: 1;
    -webkit-transform: scale3d(0.5, 0.5, 0.5);
            transform: scale3d(0.5, 0.5, 0.5);
}
to {
    opacity: 0;
    -webkit-transform: scale3d(0, 0, 0);
            transform: scale3d(0, 0, 0);
}
}
@keyframes zoomOut {
from {
    opacity: 0;
    -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
}
50% {
    opacity: 1;
    -webkit-transform: scale3d(0.5, 0.5, 0.5);
            transform: scale3d(0.5, 0.5, 0.5);
}
to {
    opacity: 0;
    -webkit-transform: scale3d(0, 0, 0);
            transform: scale3d(0, 0, 0);
}
}
@-webkit-keyframes hue {
0% {
    -webkit-filter: hue-rotate(0deg);
}
100% {
    -webkit-filter: hue-rotate(-360deg);
}
}
@keyframes hue {
0% {
    -webkit-filter: hue-rotate(0deg);
}
100% {
    -webkit-filter: hue-rotate(-360deg);
}
}
.btn-group {
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 60px;
}
.btn-group button,
.menu button {
  padding: 10px 20px;
  margin-right: 20px;
  font-size: 20px;
  font-weight: bold;
  border-radius: 8px;
  border: 2px solid #42b983;
  color: #42b983;
  transition: -webkit-transform .15s;
  transition: transform .15s;
  transition: transform .15s, -webkit-transform .15s;
  background-color: #eee;
}
.btn-group button:hover,
  .menu button:hover {
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
    color: #3ba676;
    border-color: #3ba676;
}
.btn-group button:active,
  .menu button:active {
    -webkit-transform: scale3d(0.9, 0.9, 0.9);
            transform: scale3d(0.9, 0.9, 0.9);
}
.btn-group button:disabled,
  .menu button:disabled {
    color: #ccc;
    border-color: #ccc;
    cursor: not-allowed;
}
.menu button {
  box-sizing: border-box;
  width: 160px;
  margin-right: 0;
  background-color: #42b983;
  color: #eee;
  border-color: #3ba676;
}
.menu button:hover {
    color: #fff;
    border-color: #359368;
}
.info {
  cursor: default;
}
.info span {
    margin-right: 40px;
    font-size: 16px;
    font-weight: bold;
}
.word-display {
  margin: 50px 0 50px;
  text-align: center;
  border: none;
  font-size: 70px;
  font-weight: bold;
  color: #42b983;
  background-color: #f1f1f1;
}
.my-msgbox .el-message-box__headerbtn:hover .el-message-box__close {
  color: #999;
}
.my-btn.el-button--primary {
  background-color: #42b983;
  border-color: #42b983;
}
.my-btn.el-button--primary:hover {
    background-color: #3ba676;
}
.my-cancel-btn:hover {
  border-color: #42b983;
  color: #42b983;
}
.my-msgbox-ul {
  width: 100%;
}
.my-msgbox-ul li {
    display: table;
    width: 100%;
    height: 20px;
}
.my-msgbox-ul span {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
}
.my-msgbox-ul .name,
  .my-msgbox-ul .score {
    width: 100px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.my-msgbox-ul .number,
  .my-msgbox-ul .name,
  .my-msgbox-ul .score {
    font-weight: bold;
    color: #333;
}



